.Bg{
  background-color: black;
  color: black;
  height: 1000px;
}
.navbar {
  display: flex;
  justify-content: space-between; 
  align-items: center; 
  padding: 10px 20px; 
  color: rgb(249, 249, 249);
  background-color: red;
  text-decoration: none;
  height: 50px;
}

ul {
  display: flex; 
  margin: 0;
  padding: 0; 
}

li {
  list-style-type: none;
  text-decoration: none;
  margin-left: 40px;
  
}

img {
  height: 30px; 
  text-decoration: none;
}

.navbar a{
  color: white;
  text-decoration: none;
  
}
.Link{
  color: black;
}
.navbar a {
  text-decoration: none;
}

.navbar a:visited {
  text-decoration: underline; /* Underline visited links */
}

.navbar a:focus, .navbar a.active {
  text-decoration: underline; /* Underline focused or active links */
}