.registration-container {
    background-color:white;
    box-shadow: 1px 2px 15px white;
    padding: 20px;
    border-radius: 8px;
    width: 300px; /* Adjust width as needed */
    margin: 0 auto; /* Center the container */
    transition: box-shadow; /* Add transition effect to box-shadow */
    margin-top: 200px;
    height: fit-content;
  }
  
  .registration-container:hover {
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2); /* Highlight effect on hover */
  }
  
  .registration-container h2 {
    margin-bottom: 20px;
  }
  
  .registration-container form {
    display: flex;
    flex-direction: column;
    height: auto;
  }
  
  .registration-container button {
    margin-top: 20px;
    background-color: #4caf50;
    color: white;
    border: none;
    padding: 10px 20px;
    cursor: pointer;
    border-radius: 4px;
    transition: background-color 0.3s ease; /* Add transition effect to background-color */
  }
  
  .registration-container button:hover {
    background-color: #45a049; /* Darker background color on hover */
  }
  